@import '../../../components/GlobalStyles/mixin.scss';
.homeMain {
    width: 100%;
    height: 4700px;
    position: relative;

    .home {
        width: 100%;
        height: 16.6%;
        background-image: url("../../../asset/img/SkyFulOfStars.jpg");
        @include flexCenter(space-evenly, center);

        .contentHome {
            width: 50%;
            height: 86%;
            padding: 6% 11%;
            margin-top: 7%;
            display: grid;
            place-items: center;


            .itemContentHome {
                width: 100%;
                flex-wrap: wrap;
                text-align: left;
                position: relative;

                h5 {
                    color: var(--ImpressiveColors);
                    font-size: 1.5rem;
                    font-weight: 500;

                }

                h1 {
                    color: var(--TextColor1);
                    font-size: 6rem;
                    font-weight: 750;


                }

                h3 {
                    color: var(--TextColor2);
                    font-size: 2rem;
                }

            }

            .itemContentHomeButton {
                width: 100%;
                height: 100%;

                a {

                    button {
                        width: 40%;
                        height: 100%;
                        border-radius: 30px;
                        background-color: var(--ImpressiveColors);
                        color: var(--BackgrountColors1);
                        font-size: 1rem;
                        border: none;
                        font-weight: 650;
                    }

                    button:hover {
                        @include buttonHover;

                    }
                }
            }
        }

        .imgHome {
            width: 40%;
            height: 86%;
            padding: 2% 5% 0 5%;
            position: relative;
            margin-top: 7%;
            @include flexCenter(space-evenly, center);

            img {
                @include image;
                z-index: 1;
            }

            .media {
                position: absolute;
                left: -12%;
                bottom: 5%;
                width: 25%;
                height: 15%;
                background-color: var(--BackgrountColors3);
                border: 2px solid var(--BorderColor);
                border-top-left-radius: 50px;
                border-bottom-left-radius: 50px;
                @include flexCenter(space-evenly, center);
                padding: 2%;

                .contentMedia {
                    width: 50%;
                    height: 80%;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-color: var(--BackgrountColors1);
                    border: 2px solid var(--BorderColor);
                    border-radius: 50px;

                    a {
                        i {
                            color: var(--TextColor1);
                            font-size: 2rem;
                        }
                    }
                }

                .contentMedia:hover {
                    cursor: pointer;
                }
            }

        }
    }

}
.about {
    width: 100%;
    height: 16.6%;
    background-color: var(--BackgrountColors1);
    
    .containerAbout {

        width: 100%;
        height: 100%;
        position: relative;

        h1 {
            font-size: 5rem;
            color: var(--BackgroundColorInput);
            text-transform: uppercase;
            text-align: center;
        }

        .titleAbout {
            height: 10%;
            text-align: center;
            position: absolute;
            top: 2%;
            left: 45%;
            border-bottom: 2px solid var(--ImpressiveColors);

            h2 {
                font-size: 1.5rem;
                color: var(--ImpressiveColors);
                text-transform: uppercase;
                text-align: center;
            }
        }

        .contentAbout {
            width: 100%;
            height: 95%;
            display: flex;
            padding: 2% 0%;

            .imgAbout {
                width: 50%;
                height: 100%;


                img {
                    @include image;
                }
            }

            .itemAbout {
                width: 50%;
                height: 100%;
                padding: 0% 5%;


                .titleItemAbout {
                    width: 100%;
                    flex-wrap: wrap;
                    margin: 0 0 5% 0;

                    h2 {
                        color: var(--TextColor1);
                        font-size: 2.1rem;
                        font-weight: 300;
                    }

                    h5 {
                        color: var(--ImpressiveColors);
                        font-size: 1.2rem;
                        font-weight: 300;

                    }

                    p {
                        color: var(--TextColor2);
                        font-size: 1rem;
                        font-weight: 300;
                    }
                }

                .titleItemAboutPersonalInformation {
                    width: 100%;

                    .contentTitleItemAbout {
                        @include flexCenter(space-between, center);
                        margin: 4% 0;
                        justify-content: flex-start;


                        .itemTitle {
                            width: 20%;

                            p {
                                color: var(--TextColor2);
                                font-size: 1rem;
                                font-weight: 300;
                                text-align: left;
                            }

                        }

                        .itemContent {
                            margin-left: 5%;
                            text-align: start;
                            width: 70%;

                            p {
                                color: var(--TextColor2);
                                font-size: 1rem;
                                font-weight: 300;
                                text-align: left;
                            }

                        }

                    }
                }

                .titleItemDownloadAbout {
                    width: 100%;
                    height: 9%;

                    a {
                        button {
                            width: 26%;
                            height: 100%;
                            @include borderRadius;
                            font-size: 0.7rem;

                        }

                        button:hover {
                            @include buttonHover;

                        }

                    }
                }
            }
        }
    }
}

