.Project {
    width: 100%;
    height: 16.6%;
    background-color: var(--BackgrountColors1);
    padding: 0% 10%;

    .containerProject {
        width: 100%;
        height: 100%;
        position: relative;

        h1 {
            font-size: 5rem;
            color: var(--BackgroundColorInput);
            text-transform: uppercase;
            text-align: center;
        }

        .titleProject {
            height: 8%;
            text-align: center;
            position: absolute;
            top: 2%;
            left: 45%;
            border-bottom: 2px solid var(--ImpressiveColors);

            h2 {
                font-size: 1.5rem;
                color: var(--ImpressiveColors);
                text-transform: uppercase;
                text-align: center;
            }
        }

        .contentProject {
            width: 100%;
            height: 88%;


            .listContentProject {
                display: flex;
                width: 100%;
                height: 90%;
                flex-wrap: wrap;
                overflow-x: auto;
                transition: all linear;
                @include customScrollbar();


                .itemListContentProject {
                    width: 30%;
                    height: 40%;
                    position: relative;
                    margin: 2.5% 1.6%;
                    


                    img {
                        @include image;
                        z-index: -99999;
                    }

                    .contentItem {
                        position: absolute;
                        width: 92%;
                        height: 92%;
                        top: 4%;
                        left: 4%;
                        display: grid;
                        place-items: center;
                        align-content: center;
                        align-items: end;

                        h2 {
                            width: 100%;
                            flex-wrap: wrap;
                            color: var(--TextColor1);
                            text-align: center;
                            font-size: 1.7rem;
                            display: none;
                        }

                        p {
                            display: none;
                            width: 100%;
                            flex-wrap: wrap;
                            color: var(--TextColor2);
                            text-align: center;
                            font-size: 1rem;
                        }
                    }

                    &:hover {
                        position: relative;
                        z-index: 1;

                        &::before {
                            content: '';
                            position: absolute;
                            top: -10;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            background: rgba(0, 0, 0, 0.2);
                            z-index: 2;
                        }

                        .contentItem {
                            opacity: 1;
                            border: 1px solid #fdfeff;

                            h2 {
                                display: block; // Hiển thị h2 khi hover
                            }

                            p {
                                display: block; // Hiển thị p khi hover
                            }
                        }

                        background-color: var(--BackgrountColors2);
                        cursor: pointer;
                    }

                }
            }

            .loadMore {
                width: 100%;
                height: 10%;
                @include flexCenter(center, center);

                a {
                    width: 20%;
                    height: 80%;

                    button {
                        width: 100%;
                        height: 100%;
                        @include borderRadius;
                        font-size: 1.2rem;
                    }

                    button:hover {
                        @include buttonHover;

                    }
                }
            }
        }
    }
}

