form {
    width: 100%;
    height: 87%;
    padding-right: 5%;

    input,
    textarea {
        width: 100%;
        height: 13%;
        margin: 3% 0;
        padding-left: 5%;
        color: var(--TextColor2);
        border: 1px solid var(--TextColor2);
        background-color: #101624;
        border-radius: 7px;
        font-size: 1.1rem;
        font-weight: 300;
        transition: all .4s ease;
    }
    @include input(input);

    input:focus,
    textarea:focus {
        border-color: var(--ImpressiveColors);
        outline: 0;
    }

    textarea {
        height: 20%;
        padding: 5%;
        @include customScrollbar;
    }

    button {
        width: 30%;
        height: 10%;
        @include borderRadius;
    }

    button:hover {
        @include buttonHover;

    }

}

.ContactInfro {
    width: 50%;
    height: 100%;

    h2 {
        font-size: 2rem;
        color: var(--TextColor1);
